<script>
  import mainBottom from '@/components/OrderImport/mainBottom'//订单管理main-bottom
  import uploadExcel from '@/components/uploadExcel1'
  import uploadTxt from '@/components/uploadTxt'
export default {
  data(){
    return {
      orderImportDialogTableVisible: false,
      // 表格高度
      tableHeight: `85.5vh`,
      total: 0,//总条数
      productNum: 0,
      currentPage: 1,//第几页
      pagerCount: 5,//按钮数量
      pageSize: 50,//每页多少条
      uploadData: {
        action: `http://api.321.design/v1/excelList?u_id=${sessionStorage.getItem('selectUId')}`,
        buttonName: '上传Amazon订单Excel',
        autoUpload: true,
        componentName: 'excel',
        name: 'excel'
      },
       uploadDataTxt: {
        action: `http://api.321.design/v1/orderTxtImport?u_id=${sessionStorage.getItem('selectUId')}`,
        buttonName: '上传Amazon订单Txt',
        autoUpload: true,
      },
      rules: {
        name: [ { required: true, message: '标题不能为空'} ],
      },
      tableData: [],
      multipleSelection: [],
      activeName: 'first',//标签页默认显示
      orderList:{},
    }
  },
   methods: {
    // 点击取消倒入
    cancelImport() {
      this.orderImportDialogTableVisible = false
    },
    //  点击确定导入
    async confirmImport() {
      var info = JSON.stringify(this.orderList.info)
      const res = await this.api.OrderImport({info: info})
      if(res.data.code == 200) {
        this.orderImportDialogTableVisible = false
        this.$message({
          message: '导入成功',
          type: 'success'
        })
      }
      this.initData();
    },
    isBlock(data) {
      this.orderList = data
      this.orderImportDialogTableVisible = true
    },
    // 点击·1编辑
    clickEdit(data) {
      // sessionStorage.setItem('showDialog',true)
      this.tableHeight = `86vh`;
      this.$store.commit('CHANGE_ORDERIMPORT_CELL_DATA',data);
    },
    // 复制函数
    onError(e) {
      this.$message({
        message: '复制失败！',
        type: 'error'
      })
    },
    copyFun(e) {
      this.$message({
        message: '复制成功！',
        type: 'success'
      })
    },
     changeMainHeight(){
       this.tableHeight = '85.5vh';
     },
    //  limit改变
    handleSizeChange(val) {
      this.pageSize = val;
      this.initData();
    },
    // 页数改变
    handleCurrentChange(val) {
      this.currentPage = val;
      this.initData();
    },
    //删除订单
    delOrder(){
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.delFun();
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });          
      });
    },
  // 判断是否为批量提交
    judgeFun(){
      this.$confirm('是否确认生产图片的清晰度和色差都没有问题？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        let result = await this.api.batchOrder({ id: this.handleMult() });
        this.successMess('提交成功');
        this.bottomShowStatus = false;
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '取消提交'
        });          
      });
    },
    async delFun(){
      let res = await this.api.AmazonOrderDelete({
        id: `${this.handleMult()}`
      })
      // this.postRequest(`/v1/AmazonOrderDelete?id=${this.handleMult()}`)
      // .then(res => {
      if(res.data.code=='200'){
        this.$message({
          showClose: true,
          message: '删除成功',
          type: 'success'
          });
        this.initData();
      }else {
        this.$message({
          showClose: true,
          message: res.data.msg,
          type: 'error'
        });
        this.initData();
      }
    },
    // 处理多选删除   拿订单ID
    handleMult(){
      let orderID = [];
      if(this.multipleSelection.length>0){
        for (let index = 0; index < this.multipleSelection.length; index++) {
          const element = this.multipleSelection[index];
          orderID.push(element.id);
        }
      }
      return orderID;
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //表格某行单击
    handleClickCellClick(val){
      // sessionStorage.setItem('showDialog',true)
      this.tableHeight = `86vh`;
      this.$store.commit('CHANGE_ORDERIMPORT_CELL_DATA',val);
    },
    async initData(){
      let res = await this.api.AmazonOrderLists({
        limit: this.pageSize,
        page: this.currentPage
      })
      if(res.data.code=="200"){
        this.tableData =  res.data.data.data;
        this.productNum = res.data.data.total
        this.total =  res.data.data.total;
      }else {
        this.errorMess('获取订单列表失败')
      }
    }
  },
  created(){
    this.initData();
  },
  computed: {
    AmazonExcelData(){
      return this.$store.state.AmazonExcelData;
    },
    AmazonTxtData(){
      return this.$store.state.AmazonTxtData;
    }
  },
  watch: {
    AmazonExcelData: function(newVal,oldVal){
      // this.tableData = newVal.data;
      // this.total = this.tableData.length;
    this.initData();
    },
    AmazonTxtData: function(newVal,oldVal){
      // this.tableData = newVal.data;
      // this.total = this.tableData.length;
    this.initData();
    }
  },
  components: {
    uploadExcel,
    uploadTxt,
    mainBottom
  }
}
</script>


