      // let SuffixName = file.name.split(".");
      // if (SuffixName[1] != "xls" && SuffixName[1] != "xlsx") {
      //   this.$message({
      //     type: "warning",
      //     message: "只能选取后缀为.xls或.xlsx的文件进行上传"
      //   });
      //   return false;
      // }
       let fileName = file.name
       let pos = fileName.lastIndexOf('.')
       let lastName = fileName.substring(pos, fileName.length)
       if (lastName.toLowerCase() !== '.txt') {
        this.$message.warning('只能选取后缀为.txt的文件进行上传')
      }
    },
    handleChange(file) {
      // if (this.uploadData.componentsName == "addExcel") {
      //   this.$store.commit("EDIT_EXCEL_DATA", file);
      // } else {
      //   this.beforeUpload(file);
      // }
    },
    handlerError(err) {},
