<template>
  <div v-loading="loading">
    <el-upload
      class="upload-demo"
      :action="uploadData.action"
      :show-file-list="false"
      name="excel"
      :on-progress="handleProgress"
      :auto-upload="uploadData.autoUpload"
      :on-change="handleChange"
      :before-upload="beforeUpload"
      :on-success="handleSuccess"
    >
      <el-button size="mini" class="btnStyle" style="margin-right: 0px!important;text-align: left!important;">
        {{ uploadData.buttonName }}</el-button>
      <!-- <el-button size="small">导入自定义模板</el-button> -->
    </el-upload>
  </div>
</template>
<script>
export default {
  props: ["uploadData"],
  data() {
    return {
      loading: false,
      tableData: [], //文件上传完获取的数据
    };
  },
  methods: {
    handleProgress() {
      this.loading = true;
    },
    beforeUpload(file) {
      let SuffixName = file.name.split(".");
       if(this.uploadData.ext.indexOf(SuffixName[1])==-1)
      {
          this.$message({
          type: "warning",
          message: "只能选取后缀为"+this.uploadData.ext.join(",")+"的文件进行上传"
           });
          return false;
      }
      /*if (SuffixName[1] != "xls" && SuffixName[1] != "xlsx") {
        this.$message({
          type: "warning",
          message: "只能选取后缀为.xls或.xlsx的文件进行上传"
        });
        return false;
      }*/
    },
    handleChange(file) {
      if (this.uploadData.componentsName == "addExcel") {
        this.$store.commit("EDIT_EXCEL_DATA", file);
      } else {
        this.beforeUpload(file);
      }
    },
    handlerError(err) {},
    handleSuccess(res) {
      if (res.code == "200") {
        
        this.tableData = res.data;
        // this.template_name = res.data.template_name
        // this.path = res.data.path
        // this.type = res.data.type
        this.loading = false;
        // this.$store.commit("AMAZON_EXCEL_DATA", res.data);

        this.$emit("isBlock", this.tableData);
      }
      if (res.code == "201") {
        this.$message.error("格式错误");
      }
      if (res.code == "202") {
        this.$message.error("该SKU不属于当前用户");
      }
    }
  }
};
</script>
<style lang="scss" scoped>
div {
  display: inline-block;
}
</style>
