<template>
  <div class="mainBottom">
    <el-dialog title="订单详情" :visible.sync="bottomShowStatus" width="90%">
    <div class="mainBottomLeft">
      <el-form
        :model="sizeForm"
        :rules="rules"
        ref="ruleForm"
        label-width="80px"
        size="small"
        :inline-message="true"
      >
        <el-form-item label="订单号" class="width58 disInlineBlock marginBottm5">
          <el-input v-model="sizeForm.order_id" disabled></el-input>
        </el-form-item>
        <el-form-item label="物流" class="width40 disInlineBlock marginBottm5">
          <el-select v-model="sizeForm.shipping_code" @change="logisticsChange">
            <el-option
              :label="option.courier_name"
              :value="option.id"
              v-for="(option,index) in optionList"
              :key="index"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="国|州/省" class="width58 disInlineBlock marginBottm5">
          <el-select v-model="sizeForm.ship_country" placeholder="送达国家" class="width40" disabled></el-select>
          <el-select v-model="sizeForm.ship_state" placeholder="送达城市" class="width58" disabled></el-select>
        </el-form-item>
        <el-form-item label="城市" prop="ship_city" class="width40 disInlineBlock marginBottm5">
          <el-input v-model="sizeForm.ship_city"></el-input>
        </el-form-item>
        <!-- <el-form-item label="城市" prop="ship_city" class="width40 disInlineBlock marginBottm5">
          <el-input v-model="sizeForm.ship_city"></el-input>
        </el-form-item>-->
        <el-form-item
          label="详细地址"
          prop="ship_address_1"
          class="width58 disInlineBlock marginBottm5"
        >
          <el-input v-model="sizeForm.ship_address_1"></el-input>
        </el-form-item>
        <el-form-item label="邮编" class="width40 disInlineBlock marginBottm5">
          <el-input v-model="sizeForm.ship_postal_code"></el-input>
        </el-form-item>
        <el-form-item label="收件人" prop="recipient_name" class="width58 disInlineBlock marginBottm5">
          <el-input v-model="sizeForm.recipient_name"></el-input>
        </el-form-item>
        <el-form-item label="电话" class="width40 disInlineBlock marginBottm5">
          <el-input v-model="sizeForm.buyer_phone_number"></el-input>
        </el-form-item>
        <el-form-item label="订单备注" class="marginBottm5">
          <el-input type="textarea" v-model="sizeForm.delivery_Instructions"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="mainBottomRight">
      <el-table :data="tableData" style="width: 100%" height="32vh">
        <el-table-column fixed type="index" min-width="50"></el-table-column>
        <el-table-column prop="date" label="图片" min-width="80">
          <template slot-scope="scope">
            <img v-lazy="scope.row.goods_imgs[0].path" alt srcset>
          </template>
        </el-table-column>
        <el-table-column prop="goods_name" label="商品标题" min-width="150"></el-table-column>
        <el-table-column prop="goods_weight" label="重量（KG）" min-width="100"></el-table-column>
        <el-table-column prop="msrp" label="单价" min-width="100"></el-table-column>
        <el-table-column prop="quantity_purchased" label="数量" min-width="60"></el-table-column>
      </el-table>
    </div>
    <div class="bottomCon">
      <div class="costCon" style="float:left;">
        (商品合计：
        <span class="colorRed">{{ sizeForm.goods_price_total.toFixed(2) }}</span> + 快递费用：
        <span class="colorRed">{{ sizeForm.postage_total.toFixed(2) }}</span> = 订单总额：
        <span class="colorRed">{{ (sizeForm.postage_total+sizeForm.goods_price_total).toFixed(2) }}</span>)
      </div>
      <div class="costCon" style="float:right;">
        <el-button type="warning" size="mini" @click="submitForm('ruleForm')">
          <i class="iconfont icon_baocun"></i>保存修改
        </el-button>
        <!-- <el-button type="primary" size="mini" @click="lookProductionChart"><i class="weicaker-icon-tianchongxing-"></i>查看生产图</el-button> -->
        <el-button type="success" size="mini" @click="submitForm('ruleForm')">
          <i class="el-icon-check"></i>
          {{ submitInfo }}
        </el-button>
      </div>
    </div>
    <!-- <el-dialog
        width="70%"
        title="生产图"
        :visible.sync="innerVisible"
        @close="closeInner"
        append-to-body>
        <ul>
          <li v-for="(item,index) in productionChart" :key="index">
            <img v-lazy="item.path" alt="" srcset="">
          </li>
        </ul>
    </el-dialog>-->
    </el-dialog>
  </div>
</template>

<script>
export default {
  // props: ['orderIds'],
  data() {
    return {
      // 内层弹出框
      innerVisible: false,
      bottomShowStatus: false,
      // 生产图
      productionChart: [],
      // 提交按钮信息
      submitInfo: '提交订单',
      sizeForm: {
        order_id: '',
        postage_info: '',
        ship_country: '',
        ship_state: '',
        ship_address_1: '',
        delivery_Instructions: '',
        ship_city: '',
        ship_postal_code: '',
        shipping_code: '',
        postage_total: 0,
        goods_price_total: 0
      },
      optionList: [], //物流下拉框列表
      postage_total: 0.0,
      goods_price_total: 0.0,
      tableData: [], //商品列表数据
      // 批量提交订单
      // orderIdsArr: [],
      rules: {
        ship_address_1: [
          { required: true, message: '请填写详细地址', trigger: 'blur' }
        ],
        recipient_name: [
          { required: true, message: '请填写收件人', trigger: 'blur' }
        ],
        ship_city: [{ required: true, message: '请填写城市', trigger: 'blur' }]
      }
    }
  },
  methods: {
    // 关闭按钮
    closeButton() {
      this.$emit('changeMainHeight')
      this.$store.commit('CHANGE_ORDERIMPORT_CELL_DATA', '')
    },
    //物流信息发生改变
    logisticsChange(val) {
      this.postLogisticsData(
        `v1/orImportPostage?courier_id=${val}&country_id=${
          this.sizeForm.ship_country
        }`,
        { data: this.tableData }
      ).then(res => {
        if (res.data.code == '200') {
          this.$set(this.sizeForm, 'postage_total', res.data.data.postage_total)
          this.$set(
            this.sizeForm,
            'goods_price_total',
            res.data.data.goods_price_total
          )
        }
      })
    },
    //订单提交前校验
    submitForm(formName) {
      // if(this.orderIdsArr.length>1){
      //   this.judgeFun();
      // }else {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$confirm('是否确认生产图片的清晰度和色差都没有问题？', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          })
            .then(() => {
              this.$set(this.sizeForm, 'goods_info1', [])
              this.sizeForm.goods_info1.push(this.sizeForm.goods_info)
              this.submitOrder(this.sizeForm)
            })
            .catch(() => {
              this.$message({
                type: 'info',
                message: '取消提交'
              })
            })
        } else {
          return false
        }
      })
      // }
    },
    // // 判断是否为批量提交
    // judgeFun(){
    //   this.$confirm('是否确认生产图片的清晰度和色差都没有问题？', '提示', {
    //     confirmButtonText: '确定',
    //     cancelButtonText: '取消',
    //     type: 'warning'
    //   }).then(async () => {
    //     let result = await this.api.batchOrder({ id: this.orderIdsArr });
    //     this.successMess('提交成功');
    //     this.bottomShowStatus = false;
    //   }).catch(() => {
    //     this.$message({
    //       type: 'info',
    //       message: '取消提交'
    //     });
    //   });
    // },
    // dialogClose() {
    //   sessionStorage.setItem('showDialog', false)
    // },
    // 查看生产图
    lookProductionChart() {
      this.innerVisible = true
    },
    closeInner() {
      this.innerVisible = false
    },
    //订单提交
    submitOrder(data, u_id = sessionStorage.getItem('selectUId')) {
      this.postRequest(`/v1/orderSubmit`, data).then(res => {
        if (res.data.code == '200') {
          this.$message({
            type: 'success',
            message: '已添加到订单管理'
          })
        }
        if (res.data.code == '201') {
          this.$message.error('订单提交失败')
        }
        if (res.data.code == '202') {
          this.$message.error('订单号已存在')
        }
      })
    },
    //发送sku+国家名称获得mainbottom信息
    getMainBottomInfo(newVal) {
      // 2019.6.5修改
      // this.postRequest(`/v1/OrderGoodsListsAbout?ship_country=${ship_country}&quantity_purchased=${quantity_purchased}&sku=${sku}&province_name=${newVal.province_name}&city_name`)

      this.postLogisticsData(`/v1/OrderGoodsListsAbout`, newVal).then(res => {
        if (res.data.code == '200') {
          this.tableData = res.data.data.goodsLists
          this.optionList = res.data.data.postage_info
          this.productionChart = res.data.data.goodsLists[0].designimage
          this.sizeForm = newVal
          this.$set(this.sizeForm, 'postage_total', res.data.data.postage_total)
          this.$set(
            this.sizeForm,
            'goods_price_total',
            res.data.data.goods_price_total
          )
          this.$set(
            this.sizeForm,
            'shipping_code',
            res.data.data.postage_info[0].id
          )
          // this.$set(this.sizeForm,'ship_postal_code','');
        }
      })
    }
  },
  computed: {
    cellData() {
      return this.$store.state.orderImportCellData
    }
    // show() {
    //   return window.sessionStorage.getItem('showDialog')
    // }
  },
  watch: {
    // show:function() {
    // },
    cellData: function(newVal, oldVal) {
      if (newVal) {
        this.bottomShowStatus = true
        this.getMainBottomInfo(newVal)
      } else {
        this.bottomShowStatus = false
      }
    }
  }
}
</script>

